import React, { useState, useEffect } from 'react';
import {
    Box, Typography, Container, Grid, Card, CardContent,
    TextField, Button, Chip, CircularProgress, Divider,
    MenuItem, Select, FormControl, InputLabel, Paper,
    InputAdornment, Rating, CardMedia, CardActions,
    Breadcrumbs, Link, IconButton, useTheme, Pagination,
    Slider
} from '@mui/material';
import { Link as RouterLink } from 'react-router-dom';
import { useMosqueContext } from '../contexts/MosqueContext';
import HomeIcon from '@mui/icons-material/Home';
import MosqueIcon from '@mui/icons-material/Mosque';
import SearchIcon from '@mui/icons-material/Search';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import FavoriteIcon from '@mui/icons-material/Favorite';
import FavoriteBorderIcon from '@mui/icons-material/FavoriteBorder';
import DirectionsIcon from '@mui/icons-material/Directions';
import PhoneIcon from '@mui/icons-material/Phone';
import LanguageIcon from '@mui/icons-material/Language';
import MyLocationIcon from '@mui/icons-material/MyLocation';
import KaabaAnimation from '../components/animations/KaabaAnimation';

// Helper function to calculate distance (Haversine formula)
const calculateDistance = (lat1, lon1, lat2, lon2) => {
    const R = 6371; // Radius of the Earth in km
    const dLat = (lat2 - lat1) * Math.PI / 180;
    const dLon = (lon2 - lon1) * Math.PI / 180;
    const a =
        Math.sin(dLat / 2) * Math.sin(dLat / 2) +
        Math.cos(lat1 * Math.PI / 180) * Math.cos(lat2 * Math.PI / 180) *
        Math.sin(dLon / 2) * Math.sin(dLon / 2);
    const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
    const distance = R * c; // Distance in km
    return distance * 0.621371; // Convert km to miles
};

const MosqueDirectoryPage = () => {
    const theme = useTheme();
    const { favorites, toggleFavorite } = useMosqueContext();

    // API Data State
    const [apiPlaces, setApiPlaces] = useState([]);
    const [isLoading, setIsLoading] = useState(true);
    const [apiError, setApiError] = useState(null);

    // States for filtering, sorting, pagination etc.
    const [searchTerm, setSearchTerm] = useState('');
    const [sortOption, setSortOption] = useState('distance');
    const [filterOption, setFilterOption] = useState('all');
    const [filteredPlaces, setFilteredPlaces] = useState([]);
    const [currentPage, setCurrentPage] = useState(1);
    const [placeType, setPlaceType] = useState('all');
    const [userLocation, setUserLocation] = useState(null);
    const [radiusFilter, setRadiusFilter] = useState(50); // Max radius in miles (default: 50)
    const itemsPerPage = 9;
    const MAX_RADIUS = 50; // Define max radius for slider

    // Fetch user's location
    useEffect(() => {
        if (navigator.geolocation) {
            navigator.geolocation.getCurrentPosition(
                (position) => {
                    setUserLocation({
                        lat: position.coords.latitude,
                        lng: position.coords.longitude
                    });
                },
                (error) => {
                    console.error("Error getting location:", error);
                    setSortOption('name');
                },
                { enableHighAccuracy: true, timeout: 10000, maximumAge: 60000 }
            );
        } else {
            console.error("Geolocation is not supported by this browser.");
            setSortOption('name');
        }
    }, []);

    // Fetch place data from API
    useEffect(() => {
        const fetchPlaces = async () => {
            setIsLoading(true);
            setApiError(null);
            try {
                const response = await fetch('http://localhost:3000/places');
                if (!response.ok) {
                    throw new Error(`HTTP error! status: ${response.status}`);
                }
                const data = await response.json();
                const placesWithDistance = data.map(p => ({ ...p, distance: null }));
                setApiPlaces(placesWithDistance);
            } catch (error) {
                console.error("Failed to fetch places:", error);
                setApiError(`Failed to load place data: ${error.message}. Please ensure the API is running at http://localhost:3000/places.`);
            } finally {
                setIsLoading(false);
            }
        };

        fetchPlaces();
    }, []);

    // Handle filtering and sorting based on API data and user location
    useEffect(() => {
        let results = [...apiPlaces];

        // Calculate distances if user location is available
        if (userLocation) {
            results = results.map(place => {
                if (place.coordinates && place.coordinates.lat && place.coordinates.lng) {
                    const dist = calculateDistance(
                        userLocation.lat, userLocation.lng,
                        place.coordinates.lat, place.coordinates.lng
                    );
                    return { ...place, distance: dist };
                }
                return { ...place, distance: Infinity }; // Assign Infinity if coords missing
            });

            // Filter by radius if location and radius filter are set
            if (radiusFilter < MAX_RADIUS) {
                results = results.filter(place => place.distance <= radiusFilter);
            }
        } else {
            // If no user location, ensure distance is not used for filtering/sorting unless explicitly handled
            results = results.map(p => ({ ...p, distance: Infinity }));
            if (sortOption === 'distance') setSortOption('name'); // Switch sort if location lost
        }

        // Filter by search term (name or address)
        if (searchTerm) {
            results = results.filter(place =>
                (place.name && place.name.toLowerCase().includes(searchTerm.toLowerCase())) ||
                (place.address && place.address.toLowerCase().includes(searchTerm.toLowerCase()))
            );
        }

        // Filter by type
        if (placeType !== 'all') {
            results = results.filter(place => place.type && place.type.toLowerCase() === placeType.toLowerCase());
        }

        // Filter by favorites
        if (filterOption === 'favorites') {
            results = results.filter(place => place._id && favorites.includes(place._id));
        }

        // Sort the results
        if (sortOption === 'distance' && userLocation) {
            results.sort((a, b) => a.distance - b.distance);
        } else if (sortOption === 'rating') {
            results.sort((a, b) => (b.rating ?? 0) - (a.rating ?? 0));
        } else if (sortOption === 'name') {
            results.sort((a, b) => (a.name ?? '').localeCompare(b.name ?? ''));
        }

        setFilteredPlaces(results);
        setCurrentPage(1); // Reset to first page when filters/data change
    }, [searchTerm, sortOption, filterOption, placeType, favorites, apiPlaces, userLocation, radiusFilter]);

    const handleSearchChange = (e) => {
        setSearchTerm(e.target.value);
    };

    const handlePageChange = (event, value) => {
        setCurrentPage(value);
        window.scrollTo(0, 0);
    };

    // Handle radius slider change
    const handleRadiusChange = (event, newValue) => {
        setRadiusFilter(newValue);
    };

    const currentPlaces = filteredPlaces.slice(
        (currentPage - 1) * itemsPerPage,
        currentPage * itemsPerPage
    );

    const pageCount = Math.ceil(filteredPlaces.length / itemsPerPage);

    if (isLoading) {
        return (
            <Box sx={{
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
                flexDirection: 'column',
                minHeight: '60vh'
            }}>
                <CircularProgress size={60} />
                <Typography sx={{ mt: 2 }}>Loading place directory...</Typography>
            </Box>
        );
    }

    if (apiError) {
        return (
            <Box sx={{
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
                flexDirection: 'column',
                minHeight: '50vh',
                textAlign: 'center',
                p: 3
            }}>
                <Typography color="error" variant="h6">{apiError}</Typography>
                <Button
                    variant="contained"
                    sx={{ mt: 2 }}
                    onClick={() => window.location.reload()}
                >
                    Try Again
                </Button>
            </Box>
        );
    }

    return (
        <>
            <Box
                sx={{
                    background: `linear-gradient(135deg, ${theme.palette.primary.main}, ${theme.palette.primary.dark})`,
                    color: 'white',
                    pt: 8,
                    pb: 6,
                    position: 'relative'
                }}
            >
                <Container maxWidth="lg">
                    <Typography variant="h2" component="h1" gutterBottom fontWeight="500">
                        Islamic Places Directory
                    </Typography>
                    <Typography variant="h6" sx={{ maxWidth: 800, mb: 3, opacity: 0.9 }}>
                        Find mosques, Islamic centers, halal restaurants, and more near you.
                    </Typography>
                    <Breadcrumbs aria-label="breadcrumb" sx={{ color: 'rgba(255, 255, 255, 0.7)' }}>
                        <Link
                            component={RouterLink}
                            to="/"
                            sx={{
                                display: 'flex',
                                alignItems: 'center',
                                color: 'inherit',
                                '&:hover': { color: 'white' }
                            }}
                        >
                            <HomeIcon sx={{ mr: 0.5 }} fontSize="inherit" />
                            Home
                        </Link>
                        <Typography color="white" sx={{ display: 'flex', alignItems: 'center' }}>
                            <MosqueIcon sx={{ mr: 0.5 }} fontSize="inherit" />
                            Places Directory
                        </Typography>
                    </Breadcrumbs>
                </Container>

                <Box
                    sx={{
                        position: 'absolute',
                        bottom: -1,
                        left: 0,
                        width: '100%',
                        lineHeight: 0,
                        color: theme.palette.background.default
                    }}
                >
                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 100">
                        <path
                            fill="currentColor"
                            fillOpacity="1"
                            d="M0,32L48,48C96,64,192,96,288,96C384,96,480,64,576,48C672,32,768,32,864,32C960,32,1056,32,1152,42.7C1248,53,1344,75,1392,85.3L1440,96L1440,320L1392,320C1344,320,1248,320,1152,320C1056,320,960,320,864,320C768,320,672,320,576,320C480,320,384,320,288,320C192,320,96,320,48,320L0,320Z"
                        />
                    </svg>
                </Box>
            </Box>

            <Container maxWidth="lg" sx={{ py: 6 }}>
                <Grid container spacing={4}>
                    <Grid item xs={12} md={3}>
                        <Paper elevation={2} sx={{ p: 3, mb: 3, borderRadius: 2 }}>
                            <Typography variant="h6" gutterBottom>
                                Find Places
                            </Typography>

                            <Box sx={{ display: 'flex', alignItems: 'center', mb: 2, color: userLocation ? theme.palette.success.main : theme.palette.text.secondary }}>
                                <MyLocationIcon fontSize="small" sx={{ mr: 1 }} />
                                <Typography variant="body2">
                                    {userLocation ? 'Location detected' : 'Location unavailable'}
                                </Typography>
                            </Box>

                            <TextField
                                fullWidth
                                variant="outlined"
                                label="Search by Name or Address"
                                value={searchTerm}
                                onChange={handleSearchChange}
                                margin="normal"
                                InputProps={{
                                    startAdornment: (
                                        <InputAdornment position="start">
                                            <SearchIcon />
                                        </InputAdornment>
                                    ),
                                }}
                                sx={{ mb: 2 }}
                            />

                            <FormControl fullWidth margin="normal" sx={{ mb: 2 }}>
                                <InputLabel>Place Type</InputLabel>
                                <Select
                                    value={placeType}
                                    onChange={(e) => setPlaceType(e.target.value)}
                                    label="Place Type"
                                >
                                    <MenuItem value="all">All Types</MenuItem>
                                    <MenuItem value="masjid">Masjid</MenuItem>
                                    <MenuItem value="musalla">Musalla</MenuItem>
                                    <MenuItem value="islamic center">Islamic Center</MenuItem>
                                    <MenuItem value="halal restaurant">Halal Restaurant</MenuItem>
                                    <MenuItem value="halal grocery">Halal Grocery</MenuItem>
                                </Select>
                            </FormControl>

                            <FormControl fullWidth margin="normal" sx={{ mb: 2 }}>
                                <InputLabel>Filter By</InputLabel>
                                <Select
                                    value={filterOption}
                                    onChange={(e) => setFilterOption(e.target.value)}
                                    label="Filter By"
                                >
                                    <MenuItem value="all">All Places</MenuItem>
                                    <MenuItem value="favorites">My Favorites</MenuItem>
                                </Select>
                            </FormControl>

                            <FormControl fullWidth margin="normal" sx={{ mb: 2 }}>
                                <Typography gutterBottom>
                                    Distance Radius ({radiusFilter === MAX_RADIUS ? 'Any' : `${radiusFilter} mi`})
                                </Typography>
                                <Slider
                                    value={radiusFilter}
                                    onChange={handleRadiusChange}
                                    aria-labelledby="distance-radius-slider"
                                    valueLabelDisplay="auto"
                                    step={1}
                                    marks={[
                                        { value: 1, label: '1 mi' },
                                        { value: 10, label: '10' },
                                        { value: 25, label: '25' },
                                        { value: MAX_RADIUS, label: 'Any' },
                                    ]}
                                    min={1}
                                    max={MAX_RADIUS}
                                    disabled={!userLocation}
                                />
                                {!userLocation && (
                                    <Typography variant="caption" color="textSecondary">
                                        Enable location to filter by distance.
                                    </Typography>
                                )}
                            </FormControl>

                            <FormControl fullWidth margin="normal">
                                <InputLabel>Sort By</InputLabel>
                                <Select
                                    value={sortOption}
                                    onChange={(e) => setSortOption(e.target.value)}
                                    label="Sort By"
                                    disabled={!userLocation && sortOption === 'distance'}
                                >
                                    <MenuItem value="distance" disabled={!userLocation}>Distance</MenuItem>
                                    <MenuItem value="rating">Rating</MenuItem>
                                    <MenuItem value="name">Name</MenuItem>
                                </Select>
                                {!userLocation && sortOption === 'distance' && (
                                    <Typography variant="caption" color="textSecondary" sx={{ mt: 1 }}>Enable location services to sort by distance.</Typography>
                                )}
                            </FormControl>
                        </Paper>

                        <Box sx={{ textAlign: 'center', mt: 4, display: { xs: 'none', md: 'block' } }}>
                            <KaabaAnimation size={220} compact={true} />

                            <Typography variant="body2" sx={{ mt: 2, color: 'text.secondary', fontStyle: 'italic' }}>
                                "The mosques of Allah are only to be maintained by those who believe in Allah and the Last Day..."
                                <br /><span style={{ opacity: 0.8 }}>â€” Quran 9:18</span>
                            </Typography>
                        </Box>
                    </Grid>

                    <Grid item xs={12} md={9}>
                        <Box sx={{ mb: 3, display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                            <Typography variant="h5">
                                {filteredPlaces.length} {filteredPlaces.length === 1 ? 'Place' : 'Places'} Found
                            </Typography>
                        </Box>

                        {filteredPlaces.length === 0 && !isLoading ? (
                            <Paper sx={{ p: 4, textAlign: 'center' }}>
                                <MosqueIcon sx={{ fontSize: 60, color: theme.palette.text.secondary, opacity: 0.5, mb: 2 }} />
                                <Typography variant="h6" gutterBottom>
                                    No places found
                                </Typography>
                                <Typography variant="body2" color="textSecondary">
                                    {searchTerm || placeType !== 'all' || filterOption !== 'all' || (userLocation && radiusFilter < MAX_RADIUS)
                                        ? 'Try adjusting your search, filters, or distance radius.'
                                        : 'No places available or the API might be down.'}
                                </Typography>
                                <Button
                                    variant="contained"
                                    color="primary"
                                    sx={{ mt: 2 }}
                                    onClick={() => {
                                        setSearchTerm('');
                                        setPlaceType('all');
                                        setFilterOption('all');
                                        setRadiusFilter(MAX_RADIUS);
                                        setSortOption(userLocation ? 'distance' : 'name');
                                    }}
                                >
                                    Reset Filters
                                </Button>
                            </Paper>
                        ) : (
                            <Grid container spacing={3}>
                                {currentPlaces.map(place => (
                                    <Grid item xs={12} sm={6} lg={4} key={place._id}>
                                        <Card
                                            sx={{
                                                height: '100%',
                                                display: 'flex',
                                                flexDirection: 'column',
                                                transition: 'transform 0.2s',
                                                '&:hover': {
                                                    transform: 'translateY(-5px)'
                                                }
                                            }}
                                        >
                                            <CardMedia
                                                component="img"
                                                height="160"
                                                image={`https://via.placeholder.com/600x400.png?text=${encodeURIComponent(place.name || 'Islamic Place')}`}
                                                alt={place.name || 'Islamic place image'}
                                            />
                                            <CardContent sx={{ flexGrow: 1 }}>
                                                <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start' }}>
                                                    <Typography variant="h6" component={RouterLink} to={`/place/${place._id}`}
                                                        sx={{
                                                            textDecoration: 'none',
                                                            color: theme.palette.primary.main,
                                                            '&:hover': {
                                                                textDecoration: 'underline'
                                                            },
                                                            mb: 1
                                                        }}
                                                    >
                                                        {place.name || 'Unnamed Place'}
                                                    </Typography>
                                                    <IconButton
                                                        size="small"
                                                        onClick={() => toggleFavorite(place._id)}
                                                        sx={{ color: 'rgba(0,0,0,0.54)' }}
                                                    >
                                                        {favorites.includes(place._id) ? (
                                                            <FavoriteIcon sx={{ color: theme.palette.error.main }} />
                                                        ) : (
                                                            <FavoriteBorderIcon />
                                                        )}
                                                    </IconButton>
                                                </Box>

                                                {place.rating !== undefined && place.rating !== null && (
                                                    <Box sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
                                                        <Rating
                                                            value={place.rating}
                                                            precision={0.1}
                                                            size="small"
                                                            readOnly
                                                        />
                                                        {place.reviewCount !== undefined && (
                                                            <Typography variant="body2" color="text.secondary" sx={{ ml: 1 }}>
                                                                ({place.reviewCount})
                                                            </Typography>
                                                        )}
                                                    </Box>
                                                )}

                                                {place.address && (
                                                    <Box sx={{ display: 'flex', alignItems: 'flex-start', mb: 1 }}>
                                                        <LocationOnIcon fontSize="small" sx={{ color: theme.palette.text.secondary, mt: 0.3, mr: 1, flexShrink: 0 }} />
                                                        <Typography variant="body2" color="text.secondary">
                                                            {place.address}
                                                        </Typography>
                                                    </Box>
                                                )}

                                                <Box sx={{ mt: 2 }}>
                                                    {userLocation && place.distance !== null && place.distance !== Infinity && (
                                                        <Chip
                                                            size="small"
                                                            color="primary"
                                                            variant="outlined"
                                                            label={`${place.distance.toFixed(1)} mi`}
                                                            icon={<DirectionsIcon fontSize="small" />}
                                                            sx={{ mr: 1, mb: 1 }}
                                                        />
                                                    )}

                                                    {place.type && (
                                                        <Chip
                                                            size="small"
                                                            color="secondary"
                                                            label={place.type.charAt(0).toUpperCase() + place.type.slice(1)}
                                                            sx={{ mr: 1, mb: 1 }}
                                                        />
                                                    )}
                                                </Box>
                                            </CardContent>

                                            <Divider />

                                            <CardActions sx={{ justifyContent: 'space-between' }}>
                                                <Button
                                                    size="small"
                                                    startIcon={<DirectionsIcon />}
                                                    component="a"
                                                    href={place.coordinates?.lat && place.coordinates?.lng
                                                        ? `https://maps.google.com/?q=${place.coordinates.lat},${place.coordinates.lng}`
                                                        : `https://maps.google.com/?q=${encodeURIComponent(place.address || place.name || '')}`
                                                    }
                                                    target="_blank"
                                                    rel="noopener noreferrer"
                                                    disabled={!place.address && !(place.coordinates?.lat && place.coordinates?.lng)}
                                                >
                                                    Directions
                                                </Button>

                                                <Box>
                                                    {place.phone && (
                                                        <IconButton
                                                            size="small"
                                                            component="a"
                                                            href={`tel:${place.phone.replace(/[^\d+]/g, '')}`}
                                                            sx={{ mr: 1 }}
                                                            aria-label="Call"
                                                        >
                                                            <PhoneIcon fontSize="small" />
                                                        </IconButton>
                                                    )}

                                                    {place.website && (
                                                        <IconButton
                                                            size="small"
                                                            component="a"
                                                            href={place.website.startsWith('http') ? place.website : `http://${place.website}`}
                                                            target="_blank"
                                                            rel="noopener noreferrer"
                                                            aria-label="Visit website"
                                                        >
                                                            <LanguageIcon fontSize="small" />
                                                        </IconButton>
                                                    )}
                                                </Box>
                                            </CardActions>
                                        </Card>
                                    </Grid>
                                ))}
                            </Grid>
                        )}

                        {pageCount > 1 && (
                            <Box sx={{ display: 'flex', justifyContent: 'center', mt: 4 }}>
                                <Pagination
                                    count={pageCount}
                                    page={currentPage}
                                    onChange={handlePageChange}
                                    color="primary"
                                    size="large"
                                    showFirstButton
                                    showLastButton
                                />
                            </Box>
                        )}
                    </Grid>
                </Grid>
            </Container>
        </>
    );
};

export default MosqueDirectoryPage;