import React, { useRef, useEffect } from 'react';
import { Box, useTheme } from '@mui/material';

const KaabaAnimation = ({ size = 280, compact = false }) => {
    const canvasRef = useRef(null);
    const theme = useTheme();
    const animationRef = useRef(null);
    
    // Animation settings
    const pilgrims = useRef([]);
    const pilgrimCount = compact ? 30 : 60;
    const rotationSpeed = 0.003;
    const rayCount = 8;
    
    useEffect(() => {
        const canvas = canvasRef.current;
        if (!canvas) return;
        
        const ctx = canvas.getContext('2d');
        const centerX = canvas.width / 2;
        const centerY = canvas.height / 2;
        
        // Size calculations
        const kaabaSize = size * 0.25; // Kaaba size in relation to canvas
        const tawafRadius = kaabaSize * 1.65; // Radius of the tawaf circle
        const maqamSize = kaabaSize * 0.12; // Size of the Maqam Ibrahim
        
        // Initialize pilgrims
        pilgrims.current = Array(pilgrimCount).fill().map(() => {
            const angle = Math.random() * Math.PI * 2;
            const distance = tawafRadius * (0.95 + Math.random() * 0.15);
            const speed = rotationSpeed * (0.7 + Math.random() * 0.6);
            const pilgrimSize = kaabaSize * (0.05 + Math.random() * 0.04);
            
            return {
                angle,
                distance,
                speed,
                size: pilgrimSize,
                color: Math.random() > 0.5 ? '#ffffff' : '#f0f0f0'
            };
        });
        
        // Light rays positions
        const rays = Array(rayCount).fill().map((_, i) => {
            const rayAngle = (i / rayCount) * Math.PI * 2;
            return rayAngle;
        });
        
        // Animation function
        const animate = () => {
            ctx.clearRect(0, 0, canvas.width, canvas.height);
            
            // Draw light rays (behind everything)
            ctx.save();
            rays.forEach((angle) => {
                const rayLength = kaabaSize * 4;
                ctx.beginPath();
                ctx.moveTo(centerX, centerY);
                const rayEndX = centerX + Math.cos(angle) * rayLength;
                const rayEndY = centerY + Math.sin(angle) * rayLength;
                
                const gradient = ctx.createLinearGradient(centerX, centerY, rayEndX, rayEndY);
                gradient.addColorStop(0, 'rgba(212, 175, 55, 0.6)');
                gradient.addColorStop(1, 'rgba(212, 175, 55, 0)');
                
                ctx.lineTo(rayEndX, rayEndY);
                ctx.lineWidth = kaabaSize * 0.12;
                ctx.strokeStyle = gradient;
                ctx.stroke();
            });
            ctx.restore();
            
            // Draw circular tawaf path
            ctx.beginPath();
            ctx.arc(centerX, centerY, tawafRadius, 0, Math.PI * 2);
            ctx.lineWidth = 1;
            ctx.strokeStyle = 'rgba(150, 150, 150, 0.2)';
            ctx.stroke();
            
            // Draw Maqam Ibrahim (in front of the path, behind pilgrims)
            const maqamX = centerX + tawafRadius * 0.6;
            const maqamY = centerY;
            
            // Draw maqam shadow
            ctx.beginPath();
            ctx.arc(maqamX, maqamY + maqamSize * 0.1, maqamSize * 0.85, 0, Math.PI * 2);
            ctx.fillStyle = 'rgba(0, 0, 0, 0.2)';
            ctx.fill();
            
            // Draw maqam base
            ctx.beginPath();
            ctx.arc(maqamX, maqamY, maqamSize, 0, Math.PI * 2);
            ctx.fillStyle = '#d4d0c8';
            ctx.fill();
            ctx.strokeStyle = '#a9a9a9';
            ctx.lineWidth = 1;
            ctx.stroke();
            
            // Draw maqam details
            ctx.beginPath();
            ctx.arc(maqamX, maqamY, maqamSize * 0.7, 0, Math.PI * 2);
            ctx.fillStyle = '#e8e4dc';
            ctx.fill();
            
            // Draw footprints on maqam
            ctx.beginPath();
            ctx.ellipse(
                maqamX - maqamSize * 0.25, 
                maqamY, 
                maqamSize * 0.2, 
                maqamSize * 0.1, 
                0, 0, Math.PI * 2
            );
            ctx.ellipse(
                maqamX + maqamSize * 0.25, 
                maqamY, 
                maqamSize * 0.2, 
                maqamSize * 0.1, 
                0, 0, Math.PI * 2
            );
            ctx.fillStyle = 'rgba(100, 100, 100, 0.3)';
            ctx.fill();
            
            // Update and draw pilgrims
            pilgrims.current.forEach((pilgrim, i) => {
                // Update position
                pilgrim.angle += pilgrim.speed;
                
                // Calculate position
                const x = centerX + Math.cos(pilgrim.angle) * pilgrim.distance;
                const y = centerY + Math.sin(pilgrim.angle) * pilgrim.distance;
                
                // Draw pilgrim shadow
                ctx.beginPath();
                ctx.ellipse(x, y + pilgrim.size * 0.2, pilgrim.size * 0.8, pilgrim.size * 0.3, 0, 0, Math.PI * 2);
                ctx.fillStyle = 'rgba(0, 0, 0, 0.15)';
                ctx.fill();
                
                // Draw pilgrim
                ctx.beginPath();
                ctx.arc(x, y, pilgrim.size, 0, Math.PI * 2);
                ctx.fillStyle = pilgrim.color;
                ctx.fill();
                ctx.strokeStyle = '#d0d0d0';
                ctx.lineWidth = 0.5;
                ctx.stroke();
            });
            
            // Draw Kaaba
            ctx.fillStyle = '#000000';
            ctx.beginPath();
            ctx.rect(
                centerX - kaabaSize / 2,
                centerY - kaabaSize / 2,
                kaabaSize,
                kaabaSize
            );
            ctx.fill();
            
            // Draw the Kaaba's Kiswah texture - subtle pattern
            ctx.strokeStyle = 'rgba(100, 100, 100, 0.4)';
            ctx.lineWidth = 0.5;
            
            // Horizontal lines for Kiswah pattern
            for (let i = 1; i < 10; i++) {
                const y = centerY - kaabaSize / 2 + (kaabaSize / 10) * i;
                ctx.beginPath();
                ctx.moveTo(centerX - kaabaSize / 2, y);
                ctx.lineTo(centerX + kaabaSize / 2, y);
                ctx.stroke();
            }
            
            // Vertical lines for Kiswah pattern
            for (let i = 1; i < 10; i++) {
                const x = centerX - kaabaSize / 2 + (kaabaSize / 10) * i;
                ctx.beginPath();
                ctx.moveTo(x, centerY - kaabaSize / 2);
                ctx.lineTo(x, centerY + kaabaSize / 2);
                ctx.stroke();
            }
            
            // Draw the Kaaba's golden band
            const bandHeight = kaabaSize * 0.15;
            ctx.fillStyle = '#d4af37'; // Gold color
            ctx.beginPath();
            ctx.rect(
                centerX - kaabaSize / 2,
                centerY - kaabaSize / 2 + kaabaSize / 3,
                kaabaSize,
                bandHeight
            );
            ctx.fill();
            
            // Draw border around Kaaba
            ctx.strokeStyle = '#444444';
            ctx.lineWidth = 1;
            ctx.strokeRect(
                centerX - kaabaSize / 2,
                centerY - kaabaSize / 2,
                kaabaSize,
                kaabaSize
            );
            
            // Draw Kaaba door
            const doorWidth = kaabaSize * 0.3;
            const doorHeight = kaabaSize * 0.4;
            const doorX = centerX + kaabaSize * 0.05;
            const doorY = centerY - kaabaSize * 0.05;
            
            ctx.fillStyle = '#d4af37'; // Gold color for door
            ctx.beginPath();
            ctx.rect(
                doorX - doorWidth / 2,
                doorY - doorHeight / 2,
                doorWidth,
                doorHeight
            );
            ctx.fill();
            ctx.strokeStyle = '#8b7500'; // Dark gold border
            ctx.lineWidth = 1;
            ctx.strokeRect(
                doorX - doorWidth / 2,
                doorY - doorHeight / 2,
                doorWidth,
                doorHeight
            );
            
            // Door details
            ctx.fillStyle = '#8b7500';
            ctx.beginPath();
            ctx.rect(
                doorX - doorWidth / 2 + doorWidth * 0.15,
                doorY - doorHeight / 2 + doorHeight * 0.15,
                doorWidth * 0.7,
                doorHeight * 0.7
            );
            ctx.fill();
            
            // Draw the Black Stone (Hajar al-Aswad)
            const stoneSize = kaabaSize * 0.08;
            const stoneX = centerX - kaabaSize / 2;
            const stoneY = centerY - kaabaSize * 0.1;
            
            ctx.beginPath();
            ctx.arc(stoneX, stoneY, stoneSize, 0, Math.PI * 2);
            ctx.fillStyle = '#333333';
            ctx.fill();
            ctx.strokeStyle = '#d4af37'; // Gold border
            ctx.lineWidth = 2;
            ctx.stroke();
            
            // Draw a glow around the Black Stone
            ctx.beginPath();
            ctx.arc(stoneX, stoneY, stoneSize * 1.3, 0, Math.PI * 2);
            const glowGradient = ctx.createRadialGradient(
                stoneX, stoneY, stoneSize,
                stoneX, stoneY, stoneSize * 1.3
            );
            glowGradient.addColorStop(0, 'rgba(212, 175, 55, 0.5)');
            glowGradient.addColorStop(1, 'rgba(212, 175, 55, 0)');
            ctx.fillStyle = glowGradient;
            ctx.fill();
            
            animationRef.current = requestAnimationFrame(animate);
        };
        
        // Start animation
        animate();
        
        // Cleanup
        return () => {
            if (animationRef.current) {
                cancelAnimationFrame(animationRef.current);
            }
        };
    }, [size, compact]);
    
    return (
        <Box
            sx={{
                width: size,
                height: size,
                position: 'relative',
                borderRadius: '50%',
                background: `radial-gradient(circle, ${theme.palette.background.paper} 0%, ${theme.palette.background.default} 100%)`,
                boxShadow: 'inset 0 0 30px rgba(0,0,0,0.05)',
                overflow: 'hidden',
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
                mx: 'auto'
            }}
        >
            <canvas
                ref={canvasRef}
                width={size}
                height={size}
                style={{ display: 'block' }}
            />
        </Box>
    );
};

export default KaabaAnimation;